import Vue from 'vue'
import VueRouter from 'vue-router'
import NodePadMain from '../components/NodePadMain.vue'
import componentCreateText from '../components/componentCreateText.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'NodePadMain',
    component: NodePadMain
  },
  {
    path: '/componentCreateText',
    name: 'componentCreateText',
    component: componentCreateText
  }
]

const routers = new VueRouter({
  routes
})

export default routers
